import React from 'react'
import { Button, Container } from 'react-bootstrap';
import styled from 'styled-components';

const Label = styled.label`
    margin: 10px 0;
    font-weight: bold;
    text-transform: uppercase;
`;

const ContainerStyled = styled(Container)`
    height: 550px;
    padding: 0;
`;

export default function Contact() {
    return (
        <ContainerStyled fluid className="d-flex justify-content-center align-items-center " id="contact-scroll" >
            <form action="" method="POST" className="row w-50" >
                <Label htmlFor="emailForm" className="px-0  text-white" >Email</Label>
                <input type="text" placeholder="Email..." name="emailForm" className="p-2" />
                <Label htmlFor="emailForm" className="p-0 px-0 text-white">Asunto</Label>
                <input type="text" placeholder="Asunto..." name="emailForm" className="p-2" />
                <Label htmlFor="emailForm" className="p-0 px-0 text-white">Dejame un mensaje</Label>
                <textarea type="text" placeholder="Mensaje..." name="message" rows="4"  className="p-2"/>
                <Button type="submit" className="btn mt-4">Enviar</Button>
            <p className="m-4 text-center text-white">¬°Con gusto responder√© todas tus dudas y consultas! :)</p>
            </form>
        </ContainerStyled>
    )
}
